import { Environments } from '../entities/environment';
import { VERSION } from '../utility/constants';
import { NaviError, NaviErrorType, NaviErrorTypes } from '../utility/navi-error';
import windowService from './window-service';
// import { captureNaviException } from './monitoring';
import { navStateStore } from '../state/nav-state-store';

export const LOGGING_ENDPOINT = 'https://logs.nav.porsche.com';

const enum LogLevel {
  DEBUG = 'debug',
  INFO = 'info',
  ERROR = 'error'
}

class Logger {
  private version = VERSION;
  private breakpoint = 0;
  private locale: string | null = null;

  init(_: number, locale: string) {
    this.breakpoint = windowService.getBreakpoint();
    this.locale = locale;
  }

  private log(logName: string, level: LogLevel, id: string, message: string, errorType?: NaviErrorType) {
    const { env } = navStateStore.state;
    if (env === Environments.TEST) {
      return;
    }

    try {
      const log = JSON.stringify({
        name: logName,
        level: level,
        env: env,
        breakpoint: this.breakpoint,
        locale: this.locale,
        version: this.version,
        hostname: windowService.location.hostname,
        pathname: windowService.location.pathname,
        id,
        errorType: errorType || '',
        message
      });

      if (env !== Environments.PRODUCTION) {
        // eslint-disable-next-line no-console
        console[level](log);
      }
      // eslint-disable-next-line no-empty
    } catch (err) {}
  }

  info(id: string, message = '') {
    this.log('PHN_HEADER_LOG_ENTRY', LogLevel.INFO, id, message);
  }

  error(id: string, err: Error) {
    const errorType = err instanceof NaviError ? err.type : NaviErrorTypes.GENERAL;
    this.log('PHN_HEADER_LOG_ENTRY', LogLevel.ERROR, id, err.message, errorType);
    // captureNaviException(err, { breakpoint: this.breakpoint, locale: this.locale });
  }
}

export default new Logger();
